
import {
  defineComponent, onActivated, Ref, ref,
} from 'vue';

export default defineComponent({
  setup() {
    const videoRef: Ref = ref();
    /**
    keep-alive freezes video;
     */
    onActivated(() => {
      videoRef.value.load();
    });
    return {
      videoRef,
    };
  },
  name: 'Video',
  props: {
    srcMp4: String,
    srcGif: String,
    modificator: String,
    main: String,
    imageAlt: String,
  },
});
